const AnimateError = () => {
    let ErrAnim = document.querySelectorAll(".form_err");
    ErrAnim.forEach((el) => {
        el.classList.add('err_animate');
    });
    setTimeout(function() {
        ErrAnim.forEach((el) => {
            el.classList.remove('err_animate');
        });
    }, 410);
}

export default AnimateError;